'use strict'

// 1. ie、火狐浏览器、opera浏览器、国内双核浏览器的@media screen width是window.innerWidth。chrome和safari的@media screen width是document.documentElement.clientWidth
// 补充1. 后面测试发现chrome浏览器的表现和ie等浏览器一样
// 2. clientWidth在页面初始加载时等于innerWidth。之后clientWidth = innerWidth - 浏览器滚动条宽度
// 3. ie和window环境的国内双核浏览器滚动条宽度是17px；chrome、safari、火狐浏览器滚动条宽度是15px
// ie 751 768 1007 1024 1263 1280
/* 各浏览器的userAgent
  chrome: MOZILLA/5.0 (MACINTOSH; INTEL MAC OS X 10_13_6) APPLEWEBKIT/537.36 (KHTML, LIKE GECKO) CHROME/71.0.3578.98 SAFARI/537.36
  safari: MOZILLA/5.0 (MACINTOSH; INTEL MAC OS X 10_13_6) APPLEWEBKIT/605.1.15 (KHTML, LIKE GECKO) VERSION/11.1.2 SAFARI/605.1.15
  firefox: MOZILLA/5.0 (MACINTOSH; INTEL MAC OS X 10.13; RV:64.0) GECKO/20100101 FIREFOX/64.0
  opera: MOZILLA/5.0 (MACINTOSH; INTEL MAC OS X 10_13_6) APPLEWEBKIT/537.36 (KHTML, LIKE GECKO) CHROME/70.0.3538.102 SAFARI/537.36 OPR/57.0.3098.116
  360安全浏览器 ie模式： MOZILLA/4.0 (COMPATIBLE; MSIE 7.0; WINDOWS NT 6.1; WOW64; TRIDENT/5.0; SLCC2; .NET CLR 2.0.50727; .NET CLR 3.5.30729; .NET CLR 3.0.30729; MEDIA CENTER PC 6.0; INFOPATH.3; .NET4.0C; .NET4.0E)
  360安全浏览器 极速模式：MOZILLA/5.0 (WINDOWS NT 6.1; WOW64) APPLEWEBKIT/537.36 (KHTML, LIKE GECKO) CHROME/63.0.3239.132 SAFARI/537.36
  IE 9:MOZILLA/5.0 (COMPATIBLE; MSIE 9.0; WINDOWS NT 6.1; WOW64; TRIDENT/5.0)
  qq浏览器 ie模式：MOZILLA/4.0 (COMPATIBLE; MSIE 7.0; WINDOWS NT 6.1; WOW64; TRIDENT/5.0; SLCC2; .NET CLR 2.0.50727; .NET CLR 3.5.30729; .NET CLR 3.0.30729; MEDIA CENTER PC 6.0; INFOPATH.3; .NET4.0C; .NET4.0E; CORE/1.63.6821.400 QQBROWSER/10.3.3040.400)
  qq浏览器 极速模式：MOZILLA/5.0 (WINDOWS NT 6.1; WOW64) APPLEWEBKIT/537.36 (KHTML, LIKE GECKO) CHROME/63.0.3239.26 SAFARI/537.36 CORE/1.63.6821.400 QQBROWSER/10.3.3040.400
*/

function checkUserAgent () {
  var ua = navigator.userAgent.toUpperCase()
  console.info('userAgent', ua)
  var isSafari = /SAFARI/.test(ua) && !/CHROME/.test(ua)
  // 是否opera
  var isOpera = ua.indexOf('OPERA') > -1 || ua.indexOf('OPR') > -1
  var isChrome = ua.indexOf('CHROME') > -1 && ua.indexOf('WINDOWS') === -1 && !isOpera
  console.log('isSafari:', isSafari, ',isOpera:', isOpera, ',isChrome:', isChrome)
  // 判断是否是IE浏览器
  // var isIE = ua.indexOf('MSIE') > -1
  // 是否是火狐浏览器
  // var isFirefox = ua.indexOf('FIREFOX') > -1
  // return isSafari || isChrome
  return isSafari
}

const isSafariOrChrome = checkUserAgent()
function setRem () {
  // 移动端以750设计稿为准 1rem=100px
  // pc端以1920-1200设计稿为准 1rem=100px
  var clientWidth = isSafariOrChrome ? document.documentElement.clientWidth : window.innerWidth
  console.log('##setRem1:', isSafariOrChrome, ',', clientWidth, ',', document.documentElement.clientWidth, ',', window.innerWidth)
  if (clientWidth < 768) {
    // 移动端显示方案
    // document.documentElement.style.fontSize = (clientWidth * 2 / 7.5) + 'px'
    document.documentElement.style.fontSize = (clientWidth / 7.5) + 'px'
    console.log('##setRem2:', document.documentElement.style.fontSize)
  } else if (clientWidth >= 1280) {
    // 标准1280显示方案
    document.documentElement.style.fontSize = 100 + 'px'
    console.log('##setRem3:', document.documentElement.style.fontSize)
  } else if (clientWidth >= 768 && clientWidth < 1024) {
    document.documentElement.style.fontSize = 72.0 / 120 * 100 + 'px'
    // max-width:720px;
    console.log('##setRem4:', document.documentElement.style.fontSize)
  } else if (clientWidth >= 1024 && clientWidth < 1280) {
    document.documentElement.style.fontSize = 96.0 / 120 * 100 + 'px'
    // max-width:960px;
    console.log('##setRem5:', document.documentElement.style.fontSize)
  }
}

// 初始化
setRem()

// 改变窗口大小时重新设置 rem
window.onresize = function () {
  setRem()
}



